<!--  -->
<template>
  <div class="search_page">
    <div class="search_out" :class="{search_focus: isShow===true}">
      <div class="bg"></div>
      <div class="search_inn">
        <div class="search_inn flex_c">
          <input maxlength="128" type="text" placeholder="搜索音乐/MV/歌单/歌手" v-model="searchValue" class="search" @keyup.enter="goSearch()" @blur="isShow=false" @focus="isShow=true">
          <i class="icon iconfont icon-header_icon_search_" @click="goSearch()"></i>
        </div>
      </div>
      <div class="list" :class="{hot_key:isShow===true}" v-show="isShow===true">
        <p class="tit" v-show="searchValue===''">大家都在搜</p>
        <ul ref="searchList">
          <template v-if="isShow===true">
            <li v-for="(name,index) in searchList" class="list_item reg_text" :key="index">
              <span class="idx" v-show="searchValue===''">{{ index + 1 }}</span>
              <span class="key_text">{{ name }}</span>
            </li>
          </template>
        </ul>
      </div>
    </div>
    <div>
      <div class="tabs">
        <span class="text">搜索结果</span>
        <template v-for="(item,index) in tabs">
          <a href="javascript:;" :class="{active:isActive===index}" :key="index" @click="goUrl(index,item.type)">{{item.name}}</a>
        </template>
      </div>

      <!-- <keep-alive>
        <component :is="currentView"></component>
      </keep-alive> -->
      <router-view></router-view>
    </div>

  </div>
</template>

<script>

export default {
  name: "Search",
  data () {
    return {
      tabs: [
        { name: '单曲', type: "song" },
        { name: '专辑', type: "album" },
        { name: 'MV', type: "mv" },
        { name: '歌单', type: "playlist" },
        { name: '歌手', type: "singer" },
      ],
      searchList: [],
      searchValue: "",
      isShow: false,
      isActive: 0,
    };
  },

  components: {
  },

  methods: {
    goUrl (index, type) {
      this.isActive = index;
      this.$router.push({ path: type, query: { key: this.$route.query.key } });
    },
    //跳转搜索页面
    goSearch () {
      this.$store.commit("setSearchword", this.searchValue)
      this.$router.push({ path: '/search/song', query: { key: this.searchValue } })
    }
  },

  computed: {},
  watch: {
    "$route.query.key": {
      handler (newVal, oldVal) {
        this.searchValue = newVal;
      },
      immediate: true, //监听首次绑定值
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () { },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang="scss" scoped>
@import "../assets/css/font";

.search_page {
  min-width: 990px;
  max-width: 1490px;
  padding: 0 120px;
  display: block;
}
.search_out {
  position: relative;
  height: 246px;
  .bg,
  .search_inn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .bg {
    height: 100%;
    width: 98.5vw;
    min-width: 990px;
    background-color: #000;
    z-index: -1;
  }
  .search_inn {
    width: 720px;
    z-index: 20;
  }
}
input {
  line-height: normal;
  outline: none;
}
.search_inn {
  position: relative;
  z-index: 10;
  padding-left: 8px;
  border-radius: 2px;
  background: #f5f5f5;
  .search {
    display: inline-block;
    height: 54px;
    flex: 1;
    line-height: 54px;
    border: none;
    margin-right: 24px;
    padding-left: 10px;
    background: #f5f5f5;
  }
  .icon {
    font-size: 20px;
    padding: 16px;
    cursor: pointer;
    color: #999;
  }
}

.flex_c {
  display: flex;
  align-items: center;
}
.tabs {
  display: flex;
  align-items: baseline;
  margin-top: 35px;
  margin-bottom: 35px;
  .text {
    font-size: 22px;
    font-weight: 600;
    line-height: 30px;
    margin-right: 40px;
  }
}
a {
  position: relative;
  display: inline-block;
  height: 20px;
  line-height: 20px;
  margin-right: 34px;
  font-weight: 300;
  color: #333;
  text-decoration: none;
  transition: none;
  &.active {
    font-weight: 600;
    color: #000;
    &:after {
      position: absolute;
      content: "";
      left: 0;
      bottom: 1px;
      width: 100%;
      height: 6px;
      background: #31c27c;
      z-index: -1;
    }
  }

  &:hover {
    color: #000;
  }
}
</style>



